import * as R from "ramda";
import React from "react";
import Fuse from "fuse.js";
import styled from "styled-components";
import { Input, Label, Button } from "@smooth-ui/core-sc";

const Container = styled.div`
  display: flex;
  flex-direction: column;
`;

const Header = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;

  * {
    margin-right: 5px;
  }
`;

const Body = styled.div`
  display: flex;
  flex-direction: column;
`;

const SearchContainer = styled.div`
  display: flex;
  flex-direction: row;
`;

const Pagination = styled.div`
  display: flex;
  flex-direction: row;
`;

const RowContainer = styled.div`
  display: flex;
  flex-direction: row;
  border: solid;
  border-width: 1px;
  padding: 10px;
  cursor: pointer;
`;

export interface Filter {
  value: string;
  label: string;
  filter: (e: any) => boolean;
}

export interface SearchFeedProps {
  renderRow: (e: any) => any;
  limit: number;
  elements: any[];
  searchKeys: string[];
  options?: Filter[];
  submittable?: boolean;
  onSelect?: (e: any) => void;
  onEnter?: (e: string) => void;
}

const SearchFeed = ({
  renderRow,
  limit,
  elements,
  searchKeys,
  options,
  submittable,
  onSelect,
  onEnter
}: SearchFeedProps): JSX.Element => {
  const [query, setQuery] = React.useState({
    cursor: 0,
    search: null,
    option: 0
  });

  let filteredElements = elements;
  const fuse = new Fuse(elements, { id: "id", keys: searchKeys });

  if (query.search) {
    filteredElements = fuse
      .search(query.search)
      .map(id => elements.find(R.propEq("id", id)))
      .filter(e => !R.isNil(e));
  }

  const activeOption = options && options[query.option];
  if (activeOption && activeOption.filter instanceof Function) {
    filteredElements = filteredElements.filter(activeOption.filter);
  }

  const sliceIndices = [
    Math.max(0, query.cursor),
    Math.min(filteredElements.length, query.cursor + limit)
  ];

  return (
    <Container>
      <Header>
        <Label>
          <h4>Search:</h4>
        </Label>
        <SearchContainer>
          <Input
            type="text"
            value={query.search || ""}
            onChange={(e: React.FormEvent<HTMLInputElement>) =>
              setQuery({ ...query, search: e.currentTarget.value })
            }
          />
          {submittable && (
            <Button onClick={() => onEnter && onEnter(query.search)}>
              Enter
            </Button>
          )}
        </SearchContainer>
        <div>
          {options && (
            <>
              <Label>
                <h4>Ownership:</h4>
              </Label>
              <select
                value={query.option}
                onChange={e =>
                  void setQuery({
                    ...query,
                    option: parseInt(e.currentTarget.value)
                  })
                }
              >
                {options.map((option, i) => (
                  <option key={i} value={i}>
                    {option.label}
                  </option>
                ))}
              </select>
            </>
          )}
        </div>
      </Header>
      <Header>
        <Pagination>
          <Button
            onClick={() =>
              setQuery({
                ...query,
                cursor: Math.max(0, query.cursor - filteredElements.length)
              })
            }
          >
            {"<"}
          </Button>
          {R.range(
            0,
            Math.max(1, Math.floor(filteredElements.length / limit))
          ).map(page => (
            <div
              key={page}
              onClick={() =>
                void setQuery({
                  ...query,
                  cursor: Math.max(
                    0,
                    Math.min(filteredElements.length - limit, page * limit)
                  )
                })
              }
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                cursor: "pointer",
                padding: "10px",
                textDecoration:
                  page === Math.floor(query.cursor / limit)
                    ? "underline"
                    : "none"
              }}
            >
              {page + 1}
            </div>
          ))}
          <Button
            onClick={() =>
              setQuery({
                ...query,
                cursor: Math.max(
                  0,
                  Math.min(
                    filteredElements.length - limit,
                    query.cursor + filteredElements.length
                  )
                )
              })
            }
          >
            {">"}
          </Button>
        </Pagination>
      </Header>
      <br />
      <Body>
        {filteredElements.slice(...sliceIndices).map((elementProps, i) => (
          <RowContainer key={i} onClick={() => void onSelect(elementProps)}>
            {React.createElement(renderRow, { ...elementProps })}
          </RowContainer>
        ))}
      </Body>
    </Container>
  );
};

SearchFeed.defaultProps = {
  limit: 10
};

export default SearchFeed;
